.about {
  // padding-top: 19px;

  // @media screen and (min-width: $tablet) {
  // padding-top: 30px;
  // }
  @media screen and (min-width: $desktop) {
    padding-top: 130px;
  }

  &-title {
    letter-spacing: 0.03em;
    text-align: left;
    margin-bottom: 20px;

    @media screen and (min-width: $tablet) {
      margin-bottom: 30px;
    }
    @media screen and (min-width: $desktop) {
      margin-bottom: 50px;
    }
  }
  &-box {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: $tablet) {
      flex-direction: row;
    }

    &-img {
      display: block;
      max-width: 243px;
      margin-bottom: 20px;

      @media screen and (min-width: $tablet) {
        min-width: 243px;
        margin-right: 40px;
        margin-bottom: 0;
      }
      @media screen and (min-width: $desktop) {
        max-width: 530px;
        margin-right: 30px;
      }
    }

    &-content {
      width: 100%;

      @media screen and (min-width: $desktop) {
        max-width: 640px;
      }
    }
  }
  &-name {
    font-weight: 800;
    font-size: 20px;
    line-height: 1.2;
    margin-bottom: 10px;

    @media screen and (min-width: $desktop) {
      font-size: 30px;
      line-height: 1.23;
      margin-bottom: 15px;
    }
  }
  &-list {
    border-top: 1px solid var(--text-color);
    padding-top: 20px;
    margin-bottom: 20px;

    @media screen and (min-width: $desktop) {
      padding-top: 30px;
      margin-bottom: 30px;
    }
  }
  &-item {
    font-weight: 600;
    line-height: 1.21;
    letter-spacing: 0.06em;

    @media screen and (min-width: $desktop) {
      font-size: 16px;
      line-height: 1.25;
    }
    &:not(:last-child) {
      margin-bottom: 20px;

      @media screen and (min-width: $tablet) {
        margin-bottom: 30px;
      }
    }
  }
  &-text,
  &-link {
    display: inline-block;
    // font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: normal;
    color: var(--text-color);
    margin-bottom: 25px;

    @media screen and (min-width: $desktop) {
      font-size: 18px;
      line-height: 1.56;
    }

    &-first {
      padding-top: 20px;

      @media screen and (min-width: $desktop) {
        padding-top: 30px;
      }
    }

    &-last {
      margin-bottom: 10px;

      @media screen and (min-width: $desktop) {
        margin-bottom: 20px;
      }
    }
  }
  &-link {
    text-decoration: underline;
    color: var(--accent-color);
    margin-bottom: 0;
  }
}
