.problems-color {
  background-image: linear-gradient(
    to bottom,
    var(--title-color) 75%,
    var(--white-color) 75%,
    var(--white-color)
  );

  @media screen and (min-width: $tablet) {
    background-image: linear-gradient(
      to bottom,
      var(--title-color) 67%,
      var(--white-color) 67%,
      var(--white-color)
    );
  }

  @media screen and (min-width: $desktop) {
    background-image: url('../images/fire-background.svg'),
      linear-gradient(
        to bottom,
        var(--title-color) 560px,
        var(--white-color) 560px,
        var(--white-color)
      );

    background-repeat: no-repeat;
    background-position-x: 100%, 0;
    background-position-y: 87px, 0;
  }
}

.problems {
  padding-top: 50px;
  @media screen and (min-width: $tablet) {
    padding-top: 60px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 100px;
  }
  &-header {
    text-align: left;
  }
}

