.benefits-ol {
  display: block;
  width: 100%;
  counter-reset: section;
  padding: 20px 30px;
  margin-top: 30px;
  margin-bottom: 20px;
  box-shadow: var(--benefits-shadow);

  background-color: var(--white-color);

  @media screen and (min-width: $tablet) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px 100px;
  }
  @media screen and (min-width: $desktop) {
    padding: 40px 90px 70px;
    margin-top: 70px;
  }
  .benefits-li {
    position: relative;
    font-weight: 700;
    // font-size: 14px;
    line-height: 1.5;
    // letter-spacing: 0.08em;
    padding-top: 36px;

    @media screen and (min-width: $tablet) {
      width: calc((100% - 2 * 22px) / 2);
      display: block;
      // margin-top: 0px;
      padding-top: 39px;
    }
    @media screen and (min-width: $desktop) {
      width: calc((100% - 2 * 40px) / 3);
      // font-size: 20px;
      margin-top: 30px;
      .br {
        display: none;
      }
    }
    &::before {
      position: absolute;
      top: 10px;
      left: 0;
      counter-increment: section;
      content: '\2014  0'counter(section) ' ';
      color: var(--accent-color);
    }
  }
}
